.card-container {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
}

.cardz {
    background: white;
    border: 1px solid black;
    border-radius: 0.25rem;
    padding: 1.5rem;
    transform: translateX(100px);
    opacity: 0;
    transition: 150ms;
}

.cardz.show {
    transform: translateX(0);
    opacity: 1;
}