.PostThum {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 10px;
  margin-right: 10px;
  height: 482px;
  background-color: white;
  border-radius: 20px;
  > .Header {
    height: 86px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    position: relative;
    > .Avatar {
      position: absolute;
      top: 10px;
      left: 15px;
      width: 50px;
      height: 50px;
      background-color: red;
      border-radius: 999px;
      background-image: url(https://s240-ava-talk.zadn.vn/9/f/3/1/136/240/11737f99cc0b4e06e1271d461b525bca.jpg);
      background-size: cover;
    }
    > .TitleL {
      position: absolute;
      top: 5px;
      left: 75px;
      font-weight: bold;
      font-size: 20px;
    }
    > .TitleS {
      position: absolute;
      top: 32px;
      left: 75px;
      font-size: 14px;
      color: #646262;
    }
    > .Date {
      position: absolute;
      top: 50px;
      left: 75px;
      font-size: 14px;
      color: #646262;
    }
    > .Favorite {
      position: absolute;
      top: 20px;
      right: 20px;
      &:hover {
        color: #0dc5ff;
        user-select: none;
        cursor: pointer;
      }
    }
  }
  > .Body {
    height: 274px;
    /* background-color: lime; */
    > .Photo {
      background-color: lime;
      height: 100%;
      margin-left: 15px;
      margin-right: 15px;
      border-radius: 20px;
      background-image: url("http://vngate.top/Bds/B0009/Photos/B0009_T.jpg");
      background-position: center;
      background-size: cover;
    }
  }
  > .Bottom {
    height: 120px;
    position: relative;
    > .Content {
      width: 247px;
      font-size: 14px;
      position: absolute;
      left: 15px;
      top: 5px;
    }

    > .Price {
      position: absolute;
      right: 15px;
      top: 8px;
      font-size: 16px;
      font-weight: bold;
      color: #0021d1;
    }
    > .Chitiet {
      background-color: transparent;
      border: 2px solid #0dc5ff;
      color: #646262;
      padding: 3px;
      padding-left: 10px;
      padding-right: 10px;
      border-radius: 15px;
      position: absolute;
      top: 45px;
      right: 15px;
      &:hover {
        background-color: #0dc5ff;
        color: white;
        cursor: pointer;
        user-select: none;
      }
    }
    > .CopyRight {
      height: 20px;
      position: absolute;
      bottom: 10px;
      margin-left: 25px;
      > p {
        color: #646262;
        font-size: 12px;
        border-top: 1px solid #646262;
      }
    }
  }
}
